import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Pergunta } from '../pergunta';

@Component({
  selector: 'app-tiro-errado',
  templateUrl: './tiro-errado.component.html',
  styleUrls: ['./tiro-errado.component.scss']
})
export class TiroErradoComponent implements OnInit {

  @Input()
  pergunta: Pergunta;

  @Input()
  aplicarLayout: boolean;

  @Output()
  dismiss = new EventEmitter();

  exibirBotaoSair = false;

  constructor() { }

  ngOnInit() {
    setTimeout(() => {
      this.exibirBotaoSair = true;
    }, 4000);
  }

  fechar(): void {
    this.dismiss.emit();
  }
}
