import { Component, OnInit, Input, ViewChild, ElementRef, EventEmitter, Output } from '@angular/core';
import { Pontuacao } from './pontuacao';

@Component({
  selector: 'app-pontuacao',
  templateUrl: './pontuacao.component.html',
  styleUrls: ['./pontuacao.component.scss']
})
export class PontuacaoComponent implements OnInit {

  @Input()
  pontuacao: Pontuacao[];
  
  @Output()
  reset = new EventEmitter();
  
  @ViewChild('canvas', { static: true }) canvas: ElementRef<HTMLCanvasElement>;

  ctx: CanvasRenderingContext2D;
  larguraCanvas = 400;
  larguraAlvo = 300;

  totalPontos = 0;
  totalPontosFloor = 0;
  qtdTiros = 0;

  timeout: any;
  
  tiro = new Audio();

  constructor() { }

  ngOnInit() {
    this.ctx = this.canvas.nativeElement.getContext('2d');

    // this.instanciarPontuacaoFake();

    this.tiro.src = 'assets/tiro.mp3';
    this.tiro.load();

    this.desenharAlvo();

    setTimeout(() => this.desenharProximoPonto(), 2000);
    //this.desenharProximoPonto();
  }

  fechar(): void {
    if (this.timeout) {
      clearTimeout(this.timeout);
      this.timeout = undefined;
    }
    this.reset.emit();
  }

  instanciarPontuacaoFake(): void {
    this.pontuacao = [];
    for (let i=1; i<=10; i++) {
      this.pontuacao.push({acertou: Math.random() > 0.3, tempo: Math.random() * 10000, pontos: 100});
    }
    this.pontuacao.push({acertou: true, tempo: 2000, pontos: 100});
    this.pontuacao.push({acertou: true, tempo: 2500, pontos: 100});
    this.pontuacao.push({acertou: true, tempo: 3500, pontos: 100});
    this.pontuacao.push({acertou: false, tempo: 3500, pontos: 100});
    this.pontuacao.push({acertou: false, tempo: 4500, pontos: 100});
  }

  desenharAlvo(): void {
    this.qtdTiros = this.pontuacao.length;

    const raio = this.larguraAlvo / 2;
    const centro = this.larguraCanvas / 2;

    // Círculo maior, vermelho
    this.ctx.beginPath();
    this.ctx.fillStyle = 'red';
    this.ctx.arc(centro, centro, raio, 0, 2 * Math.PI);
    this.ctx.fill();

    // Círculo interno, preto
    this.ctx.beginPath();
    this.ctx.fillStyle = 'white';
    this.ctx.arc(centro, centro, raio / 5 * 4, 0, 2 * Math.PI);
    this.ctx.fill();

    // Círculo interno, vermelho
    this.ctx.beginPath();
    this.ctx.fillStyle = 'red';
    this.ctx.arc(centro, centro, raio / 5 * 3, 0, 2 * Math.PI);
    this.ctx.fill();

    // Círculo interno, preto
    this.ctx.beginPath();
    this.ctx.fillStyle = 'white';
    this.ctx.arc(centro, centro, raio / 5 * 2, 0, 2 * Math.PI);
    this.ctx.fill();

    // Círculo interno, vermelho
    this.ctx.beginPath();
    this.ctx.fillStyle = 'red';
    this.ctx.arc(centro, centro, raio / 5 * 1, 0, 2 * Math.PI);
    this.ctx.fill();
  }

  desenharProximoPonto(): void {
    this.timeout = undefined;

    if (this.pontuacao.length) {
      const raio = this.larguraAlvo / 2;
      const centro = this.larguraCanvas / 2;
      const ponto = this.pontuacao.shift();

      const tempo = ponto.tempo;
      this.totalPontos += ponto.pontos;
      this.totalPontosFloor = Math.floor(this.totalPontos / this.qtdTiros);
      let distancia;
      if (!ponto.acertou) {
        distancia = raio * 1.2;

      } else if (tempo <= 2000) {
        distancia = (raio / 5) * tempo / 2000;

      } else if (tempo <= 5000) {
        distancia = (raio / 5) * tempo / 1000;

      } else {
        distancia = raio * 0.95
      }

      const angulo = Math.random() * 2 * Math.PI;
      const distanciaX = Math.cos(angulo) * distancia;
      const distanciaY = Math.sin(angulo) * distancia;

      // Desenha o tiro
      this.ctx.beginPath();
      this.ctx.fillStyle = 'black';
      this.ctx.arc(centro + distanciaX, centro + distanciaY, 7, 0, 2 * Math.PI);
      this.ctx.fill();
      this.reproduzirTiro();

      const tempoParaEsperar = Math.min(ponto.tempo / 5, 1000);
      this.timeout = setTimeout(() => this.desenharProximoPonto(), tempoParaEsperar);
    }
  }

  reproduzirTiro() {
    this.tiro.currentTime = 0;
    this.tiro.play();
  }
}
