import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Pergunta } from '../pergunta';

@Component({
  selector: 'app-tiro-certo',
  templateUrl: './tiro-certo.component.html',
  styleUrls: ['./tiro-certo.component.scss']
})
export class TiroCertoComponent implements OnInit {

  @Input()
  pergunta: Pergunta;

  @Input()
  aplicarLayout: boolean;

  @Output()
  dismiss = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  fechar(): void {
    this.dismiss.emit();
  }
}
